.stars {
  display: flex;
}

.stars__star {
  width: 16px;
  height: 16px;
  background: center no-repeat url(../../images/star.svg);
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 :nth-child(-n + 1),
.stars--2 :nth-child(-n + 2),
.stars--3 :nth-child(-n + 3),
.stars--4 :nth-child(-n + 4),
.stars--5 :nth-child(-n + 5) {
  background: center no-repeat url(../../images/star-active.svg);
}
